:root {
  --header-light-background-color: #fff;
  --header-dark-background-color: #111;
  --main-light-background-color: #f6f6f6;
  --main-dark-background-color: #202024;
  --inactive-background-color: #1116;
  --primary-font-color: #000;
  --secondary-font-color: #4f2ee8;
  --tertiary-font-color: #11111180;
  --quaternary-font-color: #fff;
  --quinary-font-color: #fff;
  --senary-font-color: #0009;
  --septenary-font-color: #f3f3f3;
  --nonary-font-color: #ffffff80;
  --denary-font-color: #111;
  --tenth-font-color: #b4afaf;
  --primary-border-color: #111;
  --secondary-border-color: #4f2ee8;
  --tertiary-border-color: #4f2ee866;
  --quaternary-border-color: #fff;
  --quinary-border-color: #4f2ee84d;
  --senary-border-color: #111;
  --primary-icon-color: #fff;
  --secondary-icon-color: #111;
  --primary-object-background-color: #eac645;
  --secondary-object-background-color: #4f2ee8;
  --tertiary-object-background-color: #f6f6f6;
  --quaternary-object-background-color: #fff;
  --quinary-object-background-color: #202024;
}

body {
  background-color: var(--main-light-background-color);
  color: var(--denary-font-color);
  font-family: DM Sans, sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

a {
  cursor: pointer;
  text-decoration: none;
}

.container {
  max-width: 375px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
  }
}

.header {
  background-color: var(--header-light-background-color);
  border: 1.5px solid var(--primary-border-color);
  border-radius: 0 0 8px 8px;
}

.backdrop-popup {
  z-index: 550;
  background-color: var(--inactive-background-color);
  cursor: not-allowed;
  visibility: visible;
  opacity: 1;
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
  transform: scale(1);
}

.popup-is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}

.popup {
  width: 100%;
  max-width: 335px;
  border: 2px solid var(--senary-border-color);
  background-color: var(--quaternary-object-background-color);
  cursor: auto;
  border-radius: 18px;
  flex-direction: column;
  margin: auto;
  padding: 40px 25px;
  display: flex;
  position: relative;
}

.popup__btn-close {
  width: 24px;
  height: 24px;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.popup__icon-close {
  fill: var(--denary-font-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.popup__icon-close:hover, .popup__icon-close:focus {
  fill: var(--secondary-font-color);
  cursor: pointer;
}

.popup__details {
  flex-direction: column;
  display: flex;
}

.popup__photo {
  width: 100%;
  height: 408px;
  border-radius: 8px;
  margin-right: 24px;
  overflow: hidden;
}

.popup__description {
  width: 100%;
}

.popup__title {
  text-transform: uppercase;
  letter-spacing: -.64px;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 18px;
}

.popup__author {
  color: var(--tenth-font-color);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  line-height: 14px;
}

.popup__text {
  letter-spacing: -.56px;
  margin: 18px 0;
  font-size: 14px;
  line-height: 18px;
}

.popup__amazon {
  background-color: var(--quaternary-object-background-color);
  width: 115px;
  align-items: center;
  display: flex;
}

.popup__amazon-logo {
  width: 82px;
  height: 20px;
  background-image: url("amazon.b8fa90cb.png");
  background-position: 0;
  background-repeat: no-repeat;
}

.popup__amazon-logo-book {
  width: 32px;
  height: 32px;
  background-image: url("amazon-book.5a9f17e3.png");
}

.popup__btn-add, .popup__btn-remove {
  border: 2px solid var(--secondary-border-color);
  letter-spacing: -.14px;
  text-transform: uppercase;
  background-color: #0000;
  border-radius: 40px;
  margin: 58px auto 0;
  padding: 14px 23px;
  font-size: 13px;
  font-weight: 700;
  line-height: 18px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.popup__btn-add:hover, .popup__btn-add:focus, .popup__btn-remove:hover, .popup__btn-remove:focus {
  background-color: var(--secondary-object-background-color);
  color: var(--quaternary-font-color);
  cursor: pointer;
}

.popup__btn-text-hidden {
  display: none;
}

.popup__comment {
  text-align: center;
  color: var(--tertiary-font-color);
  letter-spacing: -.4px;
  max-width: 242px;
  width: 100%;
  margin: 8px auto 0;
  font-size: 10px;
  line-height: 12px;
}

.popup-dark-theme {
  background-color: var(--main-dark-background-color);
  border: 2px solid var(--quaternary-border-color);
}

.comment-dark-theme {
  color: var(--nonary-font-color);
}

@media screen and (min-width: 768px) {
  .popup {
    width: 100%;
    max-width: 579px;
    padding: 40px;
    position: relative;
  }

  .popup__btn-close {
    width: 28px;
    height: 28px;
    cursor: pointer;
    top: 24px;
    right: 24px;
  }

  .popup__details {
    flex-direction: row;
  }

  .popup__photo {
    width: 192px;
    height: 281px;
    border-radius: 8px;
    margin-right: 24px;
    overflow: hidden;
  }

  .popup__description {
    max-width: 279px;
  }

  .popup__title {
    letter-spacing: -.96px;
    margin-top: 0;
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 28px;
  }

  .popup__author {
    letter-spacing: -.56px;
    font-size: 14px;
    line-height: 18px;
  }

  .popup__text {
    margin: 20px 0;
  }

  .popup__btn-add, .popup__btn-remove {
    max-width: 499px;
    width: 100%;
    letter-spacing: -.18px;
    margin-top: 40px;
    padding: 20px 0;
    font-size: 18px;
    line-height: 24px;
  }

  .popup__comment {
    letter-spacing: -.48px;
    max-width: 324px;
    font-size: 12px;
    line-height: 14px;
  }
}

